import React, { useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Box, Button, Typography } from '@mui/material';
import Input from '../../../components/forms/input';
import CustomFormLabel from '../../../components/forms/custom-elements/CustomFormLabel';
import PageContainer from '../../../components/container/PageContainer';
import { useNavigate } from 'react-router';
import { createTable } from '../../../api';

const AddTable = (props) => {
  const { t } = useTranslation();
  const inputFile = useRef(null);
  const navigate = useNavigate();
  const [file, setFile] = useState(undefined);
  const [tableName, setTableName] = useState(undefined);
  const [tableDescription, setTableDescription] = useState(undefined);

  const handleSubmit = async () => {
    if (!tableName || tableName?.trim()?.length === 0) {
      return alert(`${t('missing_table_name')}`);
    }
    if (!tableDescription || tableDescription?.trim()?.length === 0) {
      return alert(`${t('missing_table_description')}`);
    }
    const res = await createTable({
      tableName,
      file,
      selectionType: 'file',
      table_description: tableDescription,
    });
    if (res.data) {
      navigate('/dashboards/tables');
    }
  };

  return (
    <PageContainer title="Table Details" description="Table Details">
      <div
        style={{
          marginTop: '50px',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          flexDirection: 'column',
        }}
      >
        <Typography
          sx={{
            '@media only screen and (min-width: 3600px)': {
              fontSize: '4rem',
            },
            '@media only screen and (max-width: 3600px)': {
              fontSize: '1.6rem',
            },
            fontWeight: 'bold',
            whiteSpace: 'nowrap',
            marginBottom: 4,
          }}
        >
          {t('add_new_table')}
        </Typography>
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            flexDirection: 'column',
            gap: '50px',
          }}
        >
          {!file && (
            <Input
              id={'file'}
              type="file"
              accept=".csv"
              onChange={(val) => setFile(val, 'file')}
              placeholder={'file'}
              inputFile={inputFile}
            />
          )}
          {file && (
            <Box
              sx={{
                display: 'flex',
                alignItems: 'center',
                m: 4,
              }}
            >
              <CustomFormLabel>{file?.name}</CustomFormLabel>
              <Button
                color="secondary"
                variant="contained"
                size="large"
                sx={{
                  '@media only screen and (min-width: 3600px)': {
                    fontSize: '3.2rem',
                    minWidth: '64px',
                    maxHeight: '64px',
                  },
                  '@media only screen and (max-width: 3600px)': {
                    minWidth: '40px',
                  },
                  ml: '20px',
                  mr: '30px',
                }}
                onClick={() => {
                  if (inputFile.current) {
                    inputFile.current.value = '';
                    inputFile.current.type = 'text';
                    inputFile.current.type = 'file';
                  }
                  setFile(undefined);
                }}
              >
                X
              </Button>
            </Box>
          )}
          {file && (
            <Input
              id={'text'}
              value={tableName}
              onChange={(val) => setTableName(val)}
              placeholder={'enter_table_name'}
              customheight={'1px'}
            />
          )}
          {file && (
            <Input
              id={'text'}
              value={tableDescription}
              onChange={(val) => setTableDescription(val)}
              placeholder={'enter_table_description'}
              customheight={'1px'}
            />
          )}
          {tableDescription && tableName && file && (
            <Button
              color="secondary"
              variant="contained"
              size="large"
              sx={{
                '@media only screen and (min-width: 3600px)': {
                  fontSize: '4rem',
                  minWidth: '150px',
                },
                '@media only screen and (max-width: 3600px)': {
                  minWidth: '40px',
                  m: '0 2px',
                },
              }}
              onClick={handleSubmit}
            >
              {t('add_new_table')}
            </Button>
          )}
        </Box>
      </div>
    </PageContainer>
  );
};

export default AddTable;
